body{
    background-color:#9266ff;
}
*{
    margin:0;
    padding:0;
    box-sizing: border-box;
    
}
.app{
    max-width: 500px;
    background:#3f3160;
    margin:40px auto;
    padding:30px ;
    border-radius:20px;
    box-shadow:0 2px 6px rgba(0, 0, 0,1);
}
.app h1{
    color:#fff;
    text-align:center;
    text-transform:uppercase;
    margin-bottom:10px;
}

.todo-wrap{
    display:flex;
    margin-top:16px;
    
}

.todo-wrap .todo{
    flex:50%;
    background:#91aabb;
    padding:10px;
    border-radius: 10px;
    font-size: 18px;;
}
.todo-wrap button{
    background:#a71532;
    border-radius:10px;
    margin-left:20px;
    border:none;
    color:#fff;
    padding:8px 16px;
    font-weight:bold;
    cursor:pointer;
    outline:none;
    transition:all ease 0.2s;
}

.todo-wrap button:hover{
    opacity: 0.5;
     
}
.no-todo{
    background:#91aabb;
    text-align: center;
    padding: 16px 0;
    margin :16px 0;
    font-size:20px;

}
.addtodo form{
    display:flex;
    flex-direction: column  ;

}
.addtodo{
    margin-top:32px;
}
.addtodo label{
    font-size:20px;
    font-weight: bold;
    color: #fff;
    margin-bottom:10px;
}

.addtodo input{
    background:#91aabb;
    border-radius: 10px;
    height:40px;
    width:100%;
    border:none;
    outline:none;
    font-size: 18px;
    text-indent: 10px;
    padding-right: 16px;
}
.message{
    font-size: 16px;
    text-align: center;
    padding:16px 0;
}
.success{
    background:#47f071;
}
.danger{
    background: #e95e55;
}